* {
	box-sizing: border-box;
}

//========================swiper defaul=====================================//

.swiper {
	&-container {
		width: 100%;
		overflow: hidden;
		position: relative;
	}

	&-wrapper {
		display: flex;
		width: 100%;
		height: 100%;
	}

	&-slide {
		flex-shrink: 0;
	}
}

/* pagination */
.swiper-pagination {
	position: absolute;
	text-align: center;
	transition: .3s opacity;
	z-index: 10;
	bottom: 30px;
	left: 50%;
	transform: translateX(-50%);

	&-bullet {
		display: inline-block;
		width: 10px;
		height: 10px;
		margin: 0 15px;
		background-color: red;
		opacity: 1;
		cursor: pointer;
		border-radius: 100%;
	}

	&-bullet:hover {
		opacity: .7;
	}

	&-bullet-active {
		background-color: red;
	}

}

/* arr */
.swiper-button {
	&-prev,
	&-next {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		width: 40px;
		height: 40px;
		border-radius: 100%;
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: center;

		&::after {
			display: block;
			width: 14px;
			height: 14px;
			border-bottom: 2px solid red;
			border-left: 2px solid red;
			transition: 0.3s;
			content: '';
		}

		&:hover {
			opacity: 0.7;
		}

	}

	&-prev {
		left: 20px;
		&::after {
			transform: rotate(45deg);
		}
	}

	&-next {
		right: 20px;
		&:after {
			transform: rotate(-135deg);
		}
	}
}

//======================== style =====================================//

.bottom-slider {
	margin-bottom: 110px;
	&__container {
		max-width: 1820px;
		padding: 0;
	}
}

.bottom-slider-slide {
	text-align: center;
	@media(max-width: 410px) {
		img {
			border-radius: 20px;
			width: 100%;
		}
	}


}

